import { BaseValueObject } from '../base';

export class StatusReason extends BaseValueObject<'StatusReason', string> {
  static from(value: string): StatusReason {
    return new StatusReason(value);
  }
  protected validate(value: string): string {
    const trimmed = value.trim();

    if (trimmed.length === 0) {
      throw new Error('StatusReason must not be empty');
    }

    return value;
  }
}
